/* eslint-disable */
import React from "react";
import '../App.css';
import { Link } from 'react-router-dom';
import $ from "jquery";
import BtnHome from '../assets/btn-home.svg';
import BtnHomeOn from '../assets/btn-home-on.svg';
import BtnShop from '../assets/btn-shop.svg';
import BtnShopOn from '../assets/btn-shop-on.svg';
import BtnCommunity from '../assets/btn-community.svg';
import BtnCommunityOn from '../assets/btn-community-on.svg';
import BtnMyPage from '../assets/btn-mypage.svg';
import BtnMyPageOn from '../assets/btn-mypage-on.svg';
import BtnHealth from '../assets/btn-health.svg';
import BtnHealthOn from '../assets/btn-health-on.svg';

class Nav extends React.Component {
    componentDidMount() {
        $('.logo').click(function() {
            $('.link-tab-home').addClass('on');
            $('.link-tab-home').siblings().removeClass('on');
        })
        $('.link-tab').click(function() {
            $(this).addClass('on');
            $(this).siblings().removeClass('on');
        })
    }
    render() {
        return (
            <div>
                <header className="App-header">
                    <a className="logo"><Link to="/"><div></div></Link></a>
                    <div className="nav">
                        <Link to="/tips" className="tips">이용방법</Link>
                    </div>
                    <div className="slide-menu"></div>
                </header>
                <div className="nav-con-bottom">
                    <Link to="/" className="link-tab link-tab-home on">
                        <img src={BtnHome} className="btn-tab btn-home" />
                        <img src={BtnHomeOn} className="btn-tab btn-home on" />
                        <p>홈</p>
                    </Link>
                    <Link to="/shop" className="link-tab">
                        <img src={BtnShop} className="btn-tab btn-shop" />
                        <img src={BtnShopOn} className="btn-tab btn-shop on" />
                        <p>쇼핑몰</p>
                    </Link>
                    <Link to="/health" className="link-tab">
                        <img src={BtnHealth} className="btn-tab btn-health" />
                        <img src={BtnHealthOn} className="btn-tab btn-health on" />
                        <p>내 건강</p>
                    </Link>
                    <Link to="/community" className="link-tab">
                        <img src={BtnCommunity} className="btn-tab btn-community" />
                        <img src={BtnCommunityOn} className="btn-tab btn-community on" />
                        <p>커뮤니티</p>
                    </Link>
                    <Link to="/mypage" className="link-tab">
                        <img src={BtnMyPage} className="btn-tab btn-mypage" />
                        <img src={BtnMyPageOn} className="btn-tab btn-mypage on" />
                        <p>내 정보</p>
                    </Link>
                </div>
            </div>
        );
    }
}

export default Nav;